* {
    box-sizing: border-box;
}

body {
    font-family: VT323, monospace;
    background-color: whitesmoke;
    margin: 0
}

.memeContainer{
    max-width: 500px;
    margin-top: 10px ;
    padding: 0;
}

.header{
    width: 100%;
    height: 100px;
    background: black;
}
.tag{
    color: #fff;
    opacity: 20%;
    margin-bottom: 0;
    font-size: 50px;
    margin: 0 0 0 0.5em;

}
header {
    height: 100px;
    display: flex;
    align-items: center;
}

header > p {
    color: whitesmoke;
    font-size: 2.5em;
    margin-left: 1em;
}

.meme {
    position: relative;
    margin: 0;
}

.meme > img {
    width: 90%;
}

.meme > h2 {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.meme > .bottom {
    bottom: 0;
}

.meme > .top {
    top: 0;
}

.meme-form {
    width: 90%;
    margin: 0 ;
    display: flex;
    justify-content: space-between;
}

.meme-form > input {

    width: 45%;
    height: 40px;
}

.meme-form > button {

    border: none;
    font-family: VT323, monospace;
    font-size: 1.5em;
    letter-spacing: 1.5px;
    color: white;
    background: #6441A5;
}

.meme-form > input::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  font-family: VT323, monospace;
  font-size: 1.5em;
  text-align: cen
}
.meme-form > input::-moz-placeholder { /* Firefox 19+ */
  font-family: VT323, monospace;
  font-size: 1.5em;
  text-align: cen
}
.meme-form > input:-ms-input-placeholder { /* IE 10+ */
  font-family: VT323, monospace;
  font-size: 1.5em;
  text-align: cen
}
.meme-form > input:-moz-placeholder { /* Firefox 18- */
  font-family: VT323, monospace;
  font-size: 1.5em;
  text-align: cen
}